body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* FLEX */
.flex { display: flex; }
.horizontal, .horizontal-only { flex-direction: row; }
.vertical { flex-direction: column; }
@media screen and (max-width: 720px){
  .horizontal { flex-direction: column; }
}
.flex-1 { flex: 1; }
.flex-2 { flex: 2; }
.flex-3 { flex: 3; }
.flex-4 { flex: 4; }
.flex-5 { flex: 5; }
.flex-9 { flex: 9; }

.flex-center {
  justify-content: center;
  align-items: center; }
.flex-start {
  justify-content: flex-start;
  align-items: center; }
.flex-end {
  justify-content: flex-end;
  align-items: center; }

/* ELEMENTOS */
.container, .container-big {
  width: calc(100% - 20px);
  padding: 0 10px;
  max-width: 1200px;
  margin: 0 auto; }
.container-big {
  max-width: 1440px; }

.btn {
  padding: 10px 15px;
  font-size: 1.1rem;
  cursor: pointer;
  margin: 30px 0;
  text-align: center; }
.btn-full {
  width: 100%; }
.btn-black {
  color: #FFF;
  background-color: #222;
  border: 0; }

/* COMPONENTES */
.Topbar {
  background-color: #ddd;
  color: #333;
  padding: 5px 0;
  font-size: 0.9rem; }
.Topbar .item-top {
  font-size: 0.7rem;
  border-right: 1px solid #444;
  padding-right: 10px;
  margin-right: 10px;  }
.Topbar .item-top:last-child {
  border: 0; }
.Topbar .item-value {
  padding-left: 10px;
  font-weight: bold; }

.Header {
  padding: 15px 0; }
.input-pesquisa {
  padding: 10px 15px;
  border: 1px solid #aaaa;
  border-radius: 10px;
  width: 100%;
  font-size: 0.9rem; }
.btn-pesquisa {
  border: 0;
  background-color: transparent;
  margin-left: -40px;
  font-size: 1.2rem;
  color: #BE0912;
  cursor: pointer; }
.Header .icon-top-container {
  cursor: pointer; }
.Header .icon-top-container > .icon-top {
  color: #444;
  font-size: 1.3rem; }
.Header .icon-top-container span {
  padding: 0 10px;
  font-size: 0.8rem; }
.Header .icon-top-container .number-cart {
  color: #FFF;
  background-color: #BE0912;
  font-weight: bold;
  border-radius: 50px;
  height: 17px;
  width: 17px;
  padding: 3px;
  margin-top: -20px;
  margin-left: -3px;
}

.Categorias {
  border-top: 1px solid #aaaa;
  border-bottom: 2px solid #222;
}
.categoria-item {
  padding: 5px 0 5px 5px;
  border-right: 1px solid #cccc;
  min-width: 110px;
}
.categoria-item:hover {
  background-color: #eee;
  cursor: pointer;
}
.categoria-item:last-child {
  border-right: 0;
}
.categoria-item-img img {
  height: 20px; 
  padding: 0 5px;
}
.categoria-item-texto {
  font-size: 0.8rem;
  padding: 0 5px;
}

.HeroShop {
  padding: 30px 0; }
.breadcrumbs {
  font-size: 0.8rem;
  color: #777; }
.product-title h2 {
  font-size: 2rem;
  margin-bottom: 2px; }
.product-title span {
  font-size: 0.9rem;
  color: #666;
}

.main-pic {
  height: 100%;
  max-height: 600px; }
.mini-pics {
  padding: 20px 0; }
.mini-pic {
  max-height: 100px;
  border: 1px solid #999;
  cursor: pointer; }

.card-detalhes {
  padding: 20px;
  box-shadow: 0 0 7px #ccc;
  width: auto; } 
.estrelas {
  color: rgb(206, 183, 9);
  font-size: 1.2rem; }
.price {
  font-weight: bold;
  padding: 22px 0; }
.price > .valor {
  color: rgb(15, 117, 10);
  font-size: 2rem; }
.price > .expl {
  color: #777;
  font-size: 0.9rem; }

.opcao-item {
  padding: 10px 0;
}
.opcao-item > span {
  display: block;
  font-size: 0.8rem;
  font-weight: bold; }
.opcao-item > select {
  padding: 5px 8px;
  margin: 5px 0;
  font-size: 1rem;
  width: 100px;
  border: 1px solid #aaa;
  border-radius: 5px; }

.form-input {
  padding: 5px 8px;
  font-size: 1rem;
  margin: 5px 0;
  width: 40px;
  border-radius: 0;
  border-top-left-radius: 5px;
  border: 1px solid #aaa;
  border-bottom-left-radius: 5px; }
.margin-10-vertical {
  margin: 5px 0; }
.btn-qtd {
  color: #FFF;
  background-color: #555;
  border: 0;
  margin: 0;
  padding: 0 5px;
  font-size: 1rem;
  font-weight: bold; }
.btn-add {
  border-top-right-radius: 5px; }
.btn-sub {
  border-bottom-right-radius: 5px; }

.btn-cta {
  background-color: #BE0912;
  color: #FFF;
  padding: 15px 25px;
  font-size: 1.2rem;
  border: 0;
  font-weight: bold;
  cursor: pointer;
  margin: 22px 0; }
.btn-cta:hover {
  background-color: rgb(136, 6, 13); }
.fa-heart {
  padding: 0 15px;
  color: #aaa;
  cursor: pointer; }
.fa-heart:hover {
  color: #777; }

.hr {
  width: 100%;
  border-bottom: 1px solid #999; }

.calcular-cep > span {
  font-weight: bold; }
.input-cep {
  padding: 5px 8px;
  font-size: 0.9rem;
  margin: 6px 0;
  border-radius: 5px;
  border: 1px solid #ccc; }
.btn-cep {
  background-color: #555;
  color: #FEFEFE;
  font-size: 0.9rem;
  font-weight: bold;
  border: 0;
  border-radius: 5px;
  margin: 6px 10px 0 10px;
  padding: 5px 15px;
  cursor: pointer; }
.btn-cep:hover {
  background-color: #222; }

.FichaTecnica {
  padding: 20px 0; }
.FichaTecnica h3 {
  font-size: 2rem; }

.tabela-descricao {
  border-collapse: collapse;
}
.tabela-descricao td, .tabela-descricao th {
  padding: 15px 100px 15px 20px;
}
.tabela-descricao th {
  background-color: #333;
  color: #FFF;
}
.tabela-descricao td {
  border-bottom: 1px solid #222;
}

.Relacionados {
  padding: 50px 0; }
.card-produto {
  width: 25%; }
.card-produto .card-img img {
  width: 150px; }
.card-produto .price-container {
  font-size: 1.5rem;
  font-weight: bold; }
.card-produto .title h3 {
  margin: 20px 0 0 0;
  font-weight: 600; }

.Avaliacoes {
 padding: 40px 0; }
.card {
  padding: 25px;
  margin: 10px;
  border-radius: 12px;
  box-shadow: 0 0 7px #777;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0; }
.card .estrelas {
  color: rgb(206, 183, 9);
  font-size: 1.4rem; }
.card .qtd-avaliacao {
  font-size: 2rem;
  margin-bottom: 5px; }
.card .qtd-avaliacao-item {
  color: #777;
  font-size: 0.95rem;
}
.card-avaliacao {
  margin: 10px;
  padding: 20px;
  box-shadow: 0 0 7px #777;
  border-radius: 12px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0; }
.card-avaliacao > span {
  color: #222; }
.card-avaliacao > div {
  padding-top: 20px; }
.card-avaliacao .estrelas {
  color: rgb(206, 183, 9);
  font-size: 0.8rem; }
.card-avaliacao .data {
  color: #777;
  font-size: 0.8rem;
}

.Avisos {
  background-color: #BE0912;
  color: #FEFEFE;
  padding: 15px 0;
  font-size: 1rem; }
.padding-10-horizontal {
  padding: 0 10px; }
.text-title {
  font-weight: bold; }
.aviso-item { cursor: pointer; }
.aviso-item:hover { color: rgb(255, 223, 224); }

.Institucional {
  color: #333;
  padding: 50px 0;
  font-size: 0.9rem; }
.Institucional span {
  padding: 2px 0;
  font-size: 0.8rem;
  cursor: pointer; }
.Institucional span:hover {
  text-decoration: underline;
}

.Footer {
  color: #FCFCFC;
  background-color: #222;
  padding: 10px 0; }

@media screen and (max-width: 1400px){
  .overflow-x {
    overflow-x: scroll; }
}
.full-width { width: 100%; }

.only-mb { display: none; }
.no-mb { display: flex; }
@media screen and (max-width: 720px) {
  .only-mb { display: flex; }
  .no-mb { display: none; }

  .logo { padding: 0px 0 10px 20px; }
  .icons-header { margin-left: 30px; }
  .product-title { margin-top: -40px; }
  .tabela-descricao td, .tabela-descricao th { padding: 15px 80px 15px 20px; }
  .card-produto { width: 100%; }
  .aviso-item { padding: 10px 0; }
  .Institucional { padding-top: 0px; }

  .btn-cta {
    bottom: 0;
    position: fixed;
    margin: 0;
    width: 100%;
    margin-left: -30px; }
}